import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";
import _ from "lodash";

const notFoundImage = "404.jpg";

export const Hero = (props) => {
  const classes = makeStyles({
    root: {
      margin: "30px 0",
    },
    container: {
      position: "relative",
    },
    image: {
      width: "100%",
      height: "30vh",
      backgroundImage:
        'url("' +
        process.env.PUBLIC_URL +
        "/" +
        _.defaultTo(props.image, notFoundImage) +
        '")',
      backgroundPosition: "center",
      backgroundRepeat: "no-repeat",
      backgroundSize: "cover",
    },
    imageOverlay: {
      height: "100%",
      backgroundColor: "rgba(15, 73, 217, 0.5)",
    },
    content: {
      position: "absolute",
      top: "25%",
      left: "50%",
      width: "75%",
      textAlign: "center",
      transform: "translate(-50%)",
    },
    contentText: {
      color: "#fefefe",
      textShadow: "1px 1px #f0f0f0, 2px 3px #aaa, 4px 5px #555",
      fontSize: "8vh",
    },
    contentSubtitle: {
      textAlign: "center",
      color: "#fefefe",
      textShadow: "1px 2px #555",
      fontSize: "3vh",
    },
  })();

  return (
    <React.Fragment className={classes.root}>
      <div className={classes.container}>
        <div className={classes.content}>
          {props.subtitle && (
            <Typography variant="h5" className={classes.contentSubtitle}>
              {props.subtitle}
            </Typography>
          )}
          {props.title && (
            <Typography variant="h2" className={classes.contentText}>
              {props.title}
            </Typography>
          )}
        </div>

        <div className={classes.image} alt="Numara Çakım">
          <div className={classes.imageOverlay} />
        </div>
      </div>
    </React.Fragment>
  );
};

export default Hero;
