import React from "react";
import { makeStyles, withStyles } from "@material-ui/core/styles";
import SponsorGallery from "../components/SponsorGallery";
import LandingHero from "../components/LandingHero";
import { Button, Typography } from "@material-ui/core";

import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";
const useStyles = makeStyles({
  root: {},
});

const section1Styles = makeStyles((theme) => ({
  root: {
    padding: "30px 0px 40px 0px",
    marginBottom: 15,
    backgroundColor: "rgba(15, 73, 217, 1)",
  },
  text: {
    margin: 15,
    color: theme.palette.getContrastText("rgb(15, 73, 217, 1)"),
  },
  button: {
    margin: 5,
  },
}));

const ColorButton = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText("#fff"),
    backgroundColor: "#fff",
    "&:hover": {
      backgroundColor: "#d1d1d1",
    },
  },
}))(Button);

const logos = [
  {
    img: process.env.PUBLIC_URL + "/img/makine32.png",
    url: "https://www.mmo.org.tr/",
    text: "Makine Mühendisleri Odası",
  },
  {
    img:
      process.env.PUBLIC_URL +
      "/img/İSTANBUL_TEKNİK_ÜNİVERSİTESİ_İTÜ_LOGO_TRANSPARAN_VEKTÖR_PNG.png",
    url: "https://www.itu.edu.tr/",
    text: "İstanbul Teknik Üniversitesi",
  },
  {
    img: process.env.PUBLIC_URL + "/img/ytu_new_logo.png",
    url: "https://www.yildiz.edu.tr/",
    text: "Yıldız Teknik Üniversitesi",
  },
  {
    img: process.env.PUBLIC_URL + "/img/TNB_LOGO_Transparan.png",
    url: "https://portal.tnb.org.tr/",
    text: "Türkiye Noterler Birliği",
  },
  {
    img: process.env.PUBLIC_URL + "/img/Trafik-Tescil.png",
    url: "https://www.egm.gov.tr",
    text: "Trafik Tescil Şube Müdürlüğü",
  },
  {
    img: process.env.PUBLIC_URL + "/img/Kriminal.png",
    url: "https://www.egm.gov.tr/kriminal",
    text: "Kriminal Polis Labaratuvarı",
  },
  {
    img: process.env.PUBLIC_URL + "/img/gumruk.png",
    url: "https://raminumara.com",
    text: "T.C. Gümrük ve Ticaret Bakanlığı",
  },
  {
    img: process.env.PUBLIC_URL + "/img/Tse_logo.jpg",
    url: "https://ticaret.gov.tr/",
    text: "Türk Standartları Enstitüsü",
  },
  {
    img: process.env.PUBLIC_URL + "/img/sanayib.jpg",
    url: "https://www.sanayi.gov.tr",
    text: "T.C. Bilim, Sanayi ve Teknoloji Bakanlığı",
  },
  {
    img: process.env.PUBLIC_URL + "/img/trf-mus.jpg",
    url: "https://www.istesob.org.tr",
    text: "İstanbul Trafik Müşavirleri Odası",
  },
];

export const Section1 = (props) => {
  const classes = section1Styles();

  return (
    <section className={classes.root}>
      <Typography variant="h4" className={classes.text}>
        Rami Numara'ya başvuru nasıl yapılır?
      </Typography>
      <Link
        component={RouterLink}
        color="textPrimary"
        underline="none"
        to={process.env.PUBLIC_URL + "/basvuru"}
      >
        <ColorButton
          size="large"
          variant="contained"
          className={classes.button}
        >
          Tıklayınız
        </ColorButton>
      </Link>
    </section>
  );
};

export const Homepage = (props) => {
  const classes = useStyles();

  return (
    <section className={classes.root}>
      <LandingHero />
      <Section1 />
      <SponsorGallery items={logos} />
    </section>
  );
};

export default Homepage;
