import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Container, Paper, Typography } from "@material-ui/core";

const useStyles = makeStyles({
  root: {
    background: 'url("' + process.env.PUBLIC_URL + '/img/landing-image.jpg")',
    backgroundPosition: "center",
  },
  overlay: {
    height: "100%",
    padding: "50px",
    backgroundColor: "rgba(15, 73, 217, 0.3)",
  },
  container: {},
  paper: {
    padding: "40px 60px",
    textAlign: "left",
  },
  paragraph: {
    textIndent: "30px",
  },
});

export const AboutUs = (props) => {
  const classes = useStyles();

  return (
    <section className={classes.root}>
      <div className={classes.overlay}>
        <Container maxWidth="sm" fixed className={classes.container}>
          <Paper className={classes.paper} elevation={3}>
            <Typography
              variant="h4"
              style={{ textAlign: "center", color: "#0f49d9" }}
              gutterBottom
            >
              Hakkımızda
            </Typography>

            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              1987 yılında hizmet anlayışıyla başlamış olduğumuz sektörde
              İstanbul (Maslak) Trafik Tescil Şube Müdürlüğü'nün ilçelere
              dağıtılmasıyla 1999 yılı itibarı ile Eyüp Trafik Tescil Büro
              Amirliği'nin karşı sokağında Rami Numara şase/motor numara çakım
              merkezi olarak hizmetlerimize kesintisiz devam etmekteyiz.
            </Typography>
            <br />

            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Ana prensiplerimiz arasında kanunların öngördüğü hukuki
              zorunlulukların tamamını yerine getirerek, kendilerine devretmiş
              olduğu sorumluluğun gereklerini en iyi şekilde yerine getirmeyi
              misyon edinmiştir.
            </Typography>
            <br />

            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Rami Numara her departmandaki eğitimli, dinamik personeli ile
              gerekli olan tüm sertifikalara ve yetkiye sahip kadrosuyla
              ülkemizin önde gelen kuruluşlarına hizmet veriyor olmaktan gurur
              duymaktadır.
            </Typography>
            <br />

            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Ana felsefe olarak kaliteli hizmeti kendisine ilke edinmiş ve bu
              konuda her türlü modern yatırımlarını tamamlamıştır.
            </Typography>
          </Paper>
        </Container>
      </div>
    </section>
  );
};

export default AboutUs;
