import { BrowserRouter, Route, Switch } from "react-router-dom";

import "./App.css";
import Homepage from "./pages/Homepage";
import Application from "./pages/Application";
import PriceList from "./pages/PriceList";
import AboutUs from "./pages/AboutUs";
import PageContainer from "./components/PageContainer";
import NotFoundPage from "./pages/NotFoundPage";

const App = () => {
  const AppSwitch = () => (
    <Switch>
      <Route
        exact
        path={process.env.PUBLIC_URL + "/"}
        render={() => <Homepage />}
      ></Route>

      <Route
        exact
        path={process.env.PUBLIC_URL + "/fiyat"}
        render={() => <PriceList />}
      ></Route>

      <Route
        exact
        path={process.env.PUBLIC_URL + "/basvuru"}
        render={() => <Application />}
      ></Route>

      <Route
        exact
        path={process.env.PUBLIC_URL + "/hakkimizda"}
        render={() => <AboutUs />}
      ></Route>

      <Route path="/" render={() => <NotFoundPage />}></Route>
    </Switch>
  );

  return (
    <div className="App">
      <BrowserRouter>
        <PageContainer>
          <AppSwitch />
        </PageContainer>
      </BrowserRouter>
    </div>
  );
};

export default App;
