import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import { Container, withStyles } from "@material-ui/core";
import { currentYear } from "../logic/utils";
import Hero from "../components/Hero";

const useStyles = makeStyles({
  container: {
    margin: "50px auto",
  },
  table: {
    minWidth: 650,
  },
});

export const PriceTable = () => {
  const classes = useStyles();

  const StyledTableCell = withStyles((theme) => ({
    head: {
      //backgroundColor: theme.palette.common.black,
      color: theme.palette.common.white,
    },
    body: {
      //fontSize: 14,
    },
  }))(TableCell);

  const StyledTableRow = withStyles((theme) => ({
    root: {
      "&:nth-of-type(even)": {
        backgroundColor: "#f9f9f9",
      },
    },
  }))(TableRow);

  const createData = (name, chasis, engine) => {
    return { name, chasis, engine };
  };

  const toTRYString = (number) =>
    new Intl.NumberFormat("tr-TR", {
      style: "currency",
      currency: "TRY",
    }).format(number);

  const rows = [
    createData(
      "Otomobil, Minibüs, Kamyonet, Özel Amaçlı Taşıt, Arazi Taşıtı, Römork ve Yarı Römork",
      177.0,
      198.24
    ),
    createData(
      "Traktör (Römorklu, Römorksuz), Motosiklet ve Motorlu Bisiklet",
      165.2,
      188.8
    ),
    createData("Otobüs, Kamyon, Çekici ve Tanker", 247.8, 289.1),
  ];
  return (
    <TableContainer component={Paper} elevation={3}>
      <Table className={classes.table} aria-label="table">
        <TableHead>
          <StyledTableRow style={{ backgroundColor: "#0f49d9" }}>
            <StyledTableCell>
              <b>{currentYear} Yılı Fiyat Listesi</b>
            </StyledTableCell>
            <StyledTableCell align="center">
              <b>Şase</b>
            </StyledTableCell>
            <StyledTableCell align="center">
              <b>Motor</b>
            </StyledTableCell>
          </StyledTableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <StyledTableRow hover key={row.name}>
              <StyledTableCell component="th" scope="row">
                {row.name}
              </StyledTableCell>
              <StyledTableCell align="center">
                {toTRYString(row.chasis)}
              </StyledTableCell>
              <StyledTableCell align="center">
                {toTRYString(row.engine)}
              </StyledTableCell>
            </StyledTableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export const PriceList = (props) => {
  const classes = useStyles();

  return (
    <>
      <Hero image="img/landing-image.jpg" title="Fiyat Listesi" />
      <Container className={classes.container} maxWidth={"md"}>
        <PriceTable />
      </Container>
    </>
  );
};

export default PriceList;
