import React from "react";
import PropTypes from "prop-types";
import Sidebar from "../components/Sidebar";

import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import { makeStyles, useTheme, withStyles } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import useScrollTrigger from "@material-ui/core/useScrollTrigger";
import Fab from "@material-ui/core/Fab";
import KeyboardArrowUpIcon from "@material-ui/icons/KeyboardArrowUp";
import Zoom from "@material-ui/core/Zoom";
import { Breadcrumbs, useMediaQuery } from "@material-ui/core";

import HomeIcon from "@material-ui/icons/Home";
import LocalOfferIcon from "@material-ui/icons/LocalOffer";
import DescriptionIcon from "@material-ui/icons/Description";
import InfoIcon from "@material-ui/icons/Info";
import { Link as RouterLink } from "react-router-dom";
import Link from "@material-ui/core/Link";

const useStyles = makeStyles((theme) => ({
  root: {},
  appbar: {},
  link: {
    display: "flex",
    color: "#eee",
    padding: "0 5px",
  },
  activeLink: {
    display: "flex",
    color: "#fff",
    textDecoration: "underlined",
  },
  appbarTitle: {
    textAlign: "left",
    flexGrow: 1,
  },
  appbarIcon: {
    color: "white",
  },
  scrollToTop: {
    backgroundColor: "rgba(15, 73, 217, 1)",
    color: theme.palette.getContrastText("rgb(15, 73, 217, 1)"),
    "&:hover": {
      backgroundColor: "rgba(30, 100, 255, 1)",
    },
  },
}));

const scrollTopStyles = makeStyles((theme) => ({
  root: {
    position: "fixed",
    bottom: theme.spacing(2),
    right: theme.spacing(2),
  },
}));

function ScrollTop(props) {
  const { children, window } = props;
  const classes = scrollTopStyles();
  // Note that you normally won't need to set the window ref as useScrollTrigger
  // will default to window.
  // This is only being set here because the demo is in an iframe.
  const trigger = useScrollTrigger({
    target: window ? window() : undefined,
    disableHysteresis: true,
    threshold: 100,
  });

  const handleClick = (event) => {
    const anchor = (event.target.ownerDocument || document).querySelector(
      "#back-to-top-anchor"
    );

    if (anchor) {
      anchor.scrollIntoView({ behavior: "smooth", block: "center" });
    }
  };

  return (
    <Zoom in={trigger}>
      <div onClick={handleClick} role="presentation" className={classes.root}>
        {children}
      </div>
    </Zoom>
  );
}

ScrollTop.propTypes = {
  children: PropTypes.element.isRequired,
  /**
   * Injected by the documentation to work in an iframe.
   * You won't need it on your project.
   */
  window: PropTypes.func,
};

const ColorAppBar = withStyles((theme) => ({
  root: {
    color: theme.palette.getContrastText("rgba(15, 73, 217, 1)"),
    backgroundColor: "rgba(15, 73, 217, 1)",
  },
}))(AppBar);

export const Navbar = (props) => {
  const classes = useStyles();
  const theme = useTheme();
  const isMobile = !useMediaQuery(theme.breakpoints.up("md"));
  //console.log("Is Mobile: ", isMobile);

  const items = [
    {
      text: "Anasayfa",
      link: process.env.PUBLIC_URL + "/",
      icon: <HomeIcon />,
    },
    {
      text: "Fiyat Listesi",
      link: process.env.PUBLIC_URL + "/fiyat",
      icon: <LocalOfferIcon />,
    },
    {
      text: "Numara Başvurusu",
      link: process.env.PUBLIC_URL + "/basvuru",
      icon: <DescriptionIcon />,
    },
    {
      text: "Hakkımızda",
      link: process.env.PUBLIC_URL + "/hakkimizda",
      icon: <InfoIcon />,
    },
  ];

  const MenuItems = (props) => {
    return (
      <div>
        <Breadcrumbs
          separator="|"
          style={{ color: "#bbb" }}
          aria-label="breadcrumb"
        >
          {props.items.map((item, index) => (
            <Link
              component={RouterLink}
              color="inherit"
              to={item.link}
              className={classes.link}
            >
              {item.text}
            </Link>
          ))}
        </Breadcrumbs>
      </div>
    );
  };

  return (
    <React.Fragment className={classes.root}>
      <CssBaseline />
      <ColorAppBar className={classes.appbar}>
        <Toolbar>
          <Typography variant="h5" className={classes.appbarTitle}>
            <RouterLink
              to={process.env.PUBLIC_URL + "/"}
              style={{
                textDecoration: "none",
                color: "white",
                padding: "5px",
                border: "4px solid white",
              }}
            >
              <b>Rami Numara</b>
            </RouterLink>
          </Typography>
          {isMobile ? <Sidebar items={items} /> : <MenuItems items={items} />}
        </Toolbar>
      </ColorAppBar>
      <Toolbar id="back-to-top-anchor" />
      <ScrollTop {...props}>
        <Fab
          className={classes.scrollToTop}
          size="small"
          aria-label="scroll back to top"
        >
          <KeyboardArrowUpIcon />
        </Fab>
      </ScrollTop>
    </React.Fragment>
  );
};

export default Navbar;
