import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Typography } from "@material-ui/core";

const useStyles = makeStyles({
  root: {
    margin: "30px 0",
  },
  container: {
    position: "relative",
  },
  image: {
    width: "100%",
    height: "60vh",
    backgroundImage:
      'url("' + process.env.PUBLIC_URL + '/img/landing-image.jpg")',
    backgroundPosition: "center",
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
  },
  content: {
    position: "absolute",
    top: "25%",
    left: "50%",
    width: "75%",
    textAlign: "center",
    transform: "translate(-50%)",
  },
  contentText: {
    color: "#fefefe",
    textShadow:
      "1px 1px #f0f0f0, 2px 3px #aaa, 4px 5px #555, 0px 0px 30px #000, 0px 0px 50px #000, 0px 0px 10px #000",
    fontSize: "8vh",
  },
  contentSubtitle: {
    textAlign: "center",
    color: "#fefefe",
    textShadow: "1px 2px #555, 0px 0px 10px #000",
    fontSize: "3vh",
  },
});

export const LandingHero = (props) => {
  const classes = useStyles();

  return (
    <React.Fragment className={classes.root}>
      <div className={classes.container}>
        <div className={classes.content}>
          <Typography variant="h5" className={classes.contentSubtitle}>
            Şase / Motor
          </Typography>
          <Typography variant="h2" className={classes.contentText}>
            Numara Çakım Merkezi
          </Typography>
        </div>
        <div className={classes.image} alt="Numara Çakım"></div>
      </div>
    </React.Fragment>
  );
};

export default LandingHero;
