import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import { Container, Paper, Typography } from "@material-ui/core";

const useStyles = makeStyles({
  root: {
    background: 'url("' + process.env.PUBLIC_URL + '/img/landing-image.jpg")',
    backgroundPosition: "center",
  },
  overlay: {
    height: "100%",
    padding: "50px",
    backgroundColor: "rgba(15, 73, 217, 0.3)",
  },
  container: {},
  paper: {
    padding: "40px 60px",
    textAlign: "left",
  },
  paragraph: {
    textIndent: "30px",
  },
});

export const Application = (props) => {
  const classes = useStyles();

  return (
    <section className={classes.root}>
      <div className={classes.overlay}>
        <Container className={classes.container}>
          <Paper className={classes.paper} elevation={3}>
            <Typography
              variant="h4"
              style={{ textAlign: "center", color: "#0f49d9" }}
              gutterBottom
            >
              Rami Numara'ya Başvuru Rehberi
            </Typography>
            <Typography variant="h5" gutterBottom>
              Araç tanıtım numaraları:
            </Typography>
            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Tescile tabi araçlarda, tanıtımlarına yarayan şase /motor seri
              numaralarının bulunması zorunludur.
            </Typography>
            <br />
            <Typography variant="h6" gutterBottom>
              A:
            </Typography>
            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Araçların şase/motor seri numaralarının bulunmaması, düşmüş veya
              tamir, tadil gibi nedenlerle silinmiş veya tahrip edilmiş olması
              veya çalınan bir araca ait motor/şase numarasının başka bir araca
              ait numaralar ile değiştirildiğinin şase/motorun orijinal ve bu
              araca ait olduğunun yetkili kurumlarca verilen harf ve numaralar
              motor/şasenin tespit edilen orjinal yerine gömme olarak çakılarak
              kodlanması yapılarak MÜHÜRLENİR.
            </Typography>
            <br />
            <Typography variant="h6" gutterBottom>
              B:
            </Typography>
            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Aracın şase/motorunun araca ait olmadığının tespit edilmesi
              halinde, değişikliğin belgelendirilmesi ve teknik uygunluğunun
              sağlanması şartıyla, yeni bir numara verilir. ​yetkili kurumlarca
              verilen harf ve numaralar motor/şasenin tespit edilen orjinal
              yerine gömme olarak çakılarak kodlanması yapılarak MÜHÜRLENİR.
            </Typography>
            <br />
            <Typography variant="h6" gutterBottom>
              C:
            </Typography>
            <Typography
              variant="body1"
              className={classes.paragraph}
              gutterBottom
            >
              Aracın şase ve/veya motorunun orijinal olmasına rağmen üzerinde
              numara bulunmaması veya imalatçısı tarafından sehven hatalı
              vurulmuş olması halinde, orijinal numaraların tescil
              kayıtlarındaki numaralar ile aynı olması kaydıyla, doğru numaralar
              imalatçının/ithalatçının yetkilendirdiği kişi veya kurum
              tarafından vurulur Türkiye Noterler Birliğine bildirilir.
            </Typography>
            <br />
            <br />
            <Typography variant="subtitle2" gutterBottom>
              Rami Numara'ya yetkili kurum veya kuruşlarının tespit raporu ile
              başvuru yapılmalıdır.
            </Typography>
            <Typography variant="subtitle2" gutterBottom>
              Tescil işlemlerinin sonlandırılabilmesi için fatura ve numara
              çakım raporu ilgili kurum ve kuruluşlara verilmek üzere teslim
              edilir.
            </Typography>
            <Typography variant="subtitle2" gutterBottom>
              Şase/Motor numarası trafik tescil danışmanlığı hizmeti
              verilmektedir.
            </Typography>
          </Paper>
        </Container>
      </div>
    </section>
  );
};

export default Application;
