import { Grid } from "@material-ui/core";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles({
  image: {
    opacity: "1",
    display: "inline-block",
    height: "128px",
    transition: ".4s ease",
    backfaceVisibility: "hidden",
  },

  hoverDisplay: {
    backgroundColor: "rgba(15, 73, 217, 0.85)",
    transition: ".4s ease",
    opacity: "0",
    position: "absolute",
    top: "50%",
    left: "50%",
    textAlign: "center",
    zIndex: "1",
    transform: "translate(-50%, -50%)",
    borderRadius: "5px",
    overflow: "hidden",
  },

  container: {
    position: "relative",
    padding: "10px 0",
    "&:hover $image": {
      opacity: "0.3",
    },
    "&:hover $hoverDisplay": {
      opacity: "1",
    },
  },
  text: {
    color: "#fff",
    fontWeight: "600",
    padding: "12px 24px",
    maxWidth: "100%",
  },
});
/* TODO fix: do not activate onhover when the text is too long
 *  where the cursor is on the inactive item
 */
export const SponsorGallery = (props) => {
  const classes = useStyles();

  return (
    <Grid container direction="row" justify="space-around" alignItems="center">
      {props.items.map((i) => (
        <Grid item xs={6} sm={4}>
          <div className={classes.container}>
            <a href={i.url}>
              <img className={classes.image} src={i.img} alt={i.text}></img>
              <div>
                <div className={classes.hoverDisplay}>
                  <div className={classes.text}>{i.text}</div>
                </div>
              </div>
            </a>
          </div>
        </Grid>
      ))}
    </Grid>
  );
};

export default SponsorGallery;
